.popup-add__make {
    height: 50px;
    margin-left: 36px;
    margin-right: 36px;
    margin-top: 25px;
    background-color: rgba(0, 0, 0);
    border: 1px solid black;
    color: white;
    font-size: 18px;
    line-height: 22px;
    border-radius: 2px;
  }
  .popup-add__make:hover {
    cursor: pointer;
    opacity: 0.6;
  }
  @media screen and (max-width: 375px) {
    .popup-add__make {
      height: 50px;
      margin-left: 36px;
      margin-right: 36px;
      margin-top: 35px;
    }
  }